@mixin coverBanner {
  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, darken($color1, 10%), $color2);
    opacity: 0.7;
    z-index: -1;
  }
}

@mixin sectionGrid {
  max-width: 90%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 10vh repeat(3, auto);
  grid-gap: 1rem;
  justify-self: center;
  padding-top: 1rem;

  @include media($lg) {
    grid-template-rows: 10vh auto;
    // height: 100vh;
  }

  .section-title {
    justify-self: center;
    align-self: center;

    @include media($md) {
      font-size: 2rem;
    }

    @include media($lg) {
      font-size: 2.3rem;
      grid-row: 1;
      grid-column: span auto;
      align-self: center;
    }
  }

  ul {
    list-style: none;
    grid-row: 2;
    justify-self: center;
    display: grid;
    grid-template-columns: repeat(auto, 1fr);
    grid-gap: 2rem;

    @include media($lg) {
      display: flex;
    }

    li {
      @include media($lg) {
        padding: 0 1rem;
        grid-row: 2;
        align-self: center;
      }
    }
  }

  .card {
    display: grid;
    grid-gap: 2rem;

    .img {
      position: relative;
      transition: 0.5s;
      width: 100%;
      height: 100%;

      &:hover {
        opacity: 0.8;
        z-index: 10;

        &::before {
          content: "+";
          display: block;
          color: white;
          font-size: 6rem;
          z-index: 50;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }

        &::after {
          content: "";
          display: block;
          width: 100%;
          height: 100%;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          background: linear-gradient(180deg, darken($color1, 10%), $color2);
          opacity: 0.7;
        }
      }
    }

    .photo {
      max-width: 100%;
      position: relative;
    }

    &-content {
      .card-title {
        text-transform: uppercase;
        font-size: 1.2rem;
        font-weight: 600;
        color: lighten(#000, 30%);

        @include media($md) {
          font-size: 1.3rem;
        }
      }

      .card-text {
        margin-top: 2rem;
        opacity: 0.7;
        @include media($md) {
          font-size: 1rem;
          opacity: 0.6;
        }
      }
    }
  }

  .section-desc-btn {
    background-color: $color1;
    border: 3px solid $color1;
    border-radius: 50px;
    color: #fff;
    font-weight: bold;
    justify-self: left;
    letter-spacing: 0.1rem;
    padding: 1.25rem 3.75rem;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.6s;
    @include media($md) {
      font-size: 1.5rem;
    }
    @include media($lg) {
      font-size: 1rem;
      padding: 1rem 2.6rem;
      text-align: center;
    }
    &:hover {
      background-color: #fff;
      color: $color1;
    }
  }
}

@mixin inputBorder {
  border: 1px solid #aaa;
  border-radius: 5px;
  width: 50%;
  padding: 1rem 0.8rem;
  font-size: 0.8rem;

  &:focus {
    border-color: mix($color1, $color2, 50%);
    border-width: 2px;
  }
}

// Media queries
@mixin media($size) {
  @media (min-width: $size) {
    @content;
  }
}
