#instructors {
  @include sectionGrid;
  grid-row: 4;
  min-height: 80vh;
  @include media($xl) {
    padding-bottom: 2rem;
  }
  .card {
    text-align: center;

    .img {
      &:hover {
        &::after {
          content: "";
          display: block;
          border-radius: 50%;

          @include media($sm) {
            width: 52%;
          }

          // @include media($sm) {
          //   width: 52%;
          // }

          @include media($lg) {
            width: 100%;
          }
        }
      }
    }
    .photo {
      border-radius: 50%;
    }

    .section-desc-btn {
      justify-self: center;

      @include media($lg) {
        padding: 1rem 1.7rem;
      }
    }
  }
}
