.footer {
  grid-row: 7;
  max-width: 100%;
  background: darken($color1, 10%);
  display: grid;
  grid-template-columns: auto auto;
  color: $font-white;

  &-nav {
    align-self: center;
    justify-self: start;
    font-size: 1rem;
    padding-left: 0.5rem;

    @include media($mid) {
      padding: 0;
    }

    &-list {
      @include media($mid) {
        display: flex;
        justify-content: space-around;
      }
    }

    &-list li {
      list-style-type: none;

      @include media($mid) {
        padding: 0 1rem;
      }

      @include media($md) {
        padding: 0.5rem;
      }

      @include media($xl) {
        padding: 0;
      }

      a {
        text-decoration: none;
        color: #fff;
        opacity: 0.6;
        transition: 0.4s;
        padding: 0;

        @include media($md) {
          font-size: 1.3rem;
        }

        @include media($lg) {
          font-size: 1.5rem;
        }

        &:hover {
          opacity: 1;
        }
      }
    }
  }

  .copy {
    align-self: center;
    justify-self: end;
    font-size: 0.8rem;
    padding-right: 0.5rem;

    @include media($sm) {
      font-size: 1rem;
    }
    @include media($md) {
      font-size: 1.2rem;
    }
    @include media($lg) {
      font-size: 1.4rem;
    }
  }
}
