.content-hero {
  grid-row: 3;
  max-width: 100%;
  position: relative;
  background-image: url("../../dist/images/paralax-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top right;

  .box {
    color: $font-white;
    height: 100%;
    @include coverBanner;
    opacity: 0.99;
    display: grid;
    grid-template-rows: auto;
    grid-gap: 1.5rem;
    text-align: center;
    align-content: center;

    .content-hero-title {
      font-weight: 500;
      opacity: 0.8;
      @include media($md) {
        font-size: 2rem;
      }
    }

    .content-hero-text {
      font-weight: 300;
      opacity: 0.8;
      @include media($md) {
        padding: 0 5rem;
        font-size: 1.3rem;
        opacity: 0.5;
      }
    }
  }
}

.content-hero-anim {
  span {
    display: inline-block;
    width: 1.3rem;
    height: 2px;
    background-color: #fff;
    animation: anim1 1.2s infinite linear forwards alternate;

    @include media($md) {
      width: 1.5rem;
    }
    @for $i from 1 through 3 {
      &:nth-child(#{$i}) {
        opacity: 0.3;
        animation-delay: (0.3 * $i) * 1s;
      }
    }
  }
}

@keyframes anim1 {
  100% {
    opacity: 1;
  }
}
