.contact {
  grid-row: 5;
  max-width: 90%;
  justify-self: center;

  .section-title {
    text-align: center;
    padding: 1rem 0;
    @include media($md) {
      font-size: 2rem;
    }
    @include media($lg) {
      font-size: 2.3rem;
    }
  }

  .contact-desc {
    padding: 1rem 0;
    @include media($md) {
      font-size: 1rem;
    }
    @include media($lg) {
      font-size: 1.2rem;
    }
  }

  .form {
    input[type="text"] {
      @include inputBorder;
    }

    input[type="email"] {
      @include inputBorder;
    }

    textarea {
      @include inputBorder;
      width: 100%;
    }

    button[type="submit"] {
      border: none;
      background-color: darken($color1, 10%);
      border-radius: 50px;
      color: $font-white;
      width: 10rem;
      height: 3rem;
      padding: 10px 30px 10px 70px;
      text-transform: uppercase;
      position: relative;
      display: block;
      overflow: hidden;
      @include media($md) {
        font-size: 1.2rem;
      }
      @include media($lg) {
        padding: 10px 40px 10px 60px;
        width: 11rem;
        font-size: 1.5rem;
      }
      &::before {
        content: "";
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 1.5rem;
        transform: translate(-50%, -50%);
        width: 1.5rem;
        height: 1.5rem;
        border: 2px solid rgba(255, 255, 255, 0.5);
        border-left-color: rgba(255, 255, 255, 1);
        border-bottom-color: rgba(255, 255, 255, 1);
        border-radius: 50%;
        animation: animCircle 1s infinite linear;
      }
    }
  }
}

@keyframes animCircle {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
