.main-hero {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-gap: 1rem;
  width: 100%;
  position: relative;

  @include coverBanner;

  &-video {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    & video {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

// NAGŁÓWEK
.page-header {
  .container {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include media($md) {
      width: 100%;
      padding-top: 3rem;
    }

    @include media($lig) {
      padding-top: 1rem;
    }

    @include media($lg) {
      height: 15vh;
      width: 100%;
    }

    @include media($xl) {
      height: auto;
    }
    // LOGO
    .logo {
      padding-left: 1.5rem;
      display: inline-block;
      // &-img {
      @include media($md) {
        width: 100%;
        height: 100%;
      }

      &-img {
        // width: 100%;
        // height: 100%;

        @include media($xl) {
          // width: 70%;
        }
      }
    }
  }
}

// NAVIGATION
.page-nav {
  @include media($lg) {
    display: flex;
    justify-content: flex-end;
  }

  &-list {
    position: fixed;
    top: 80px;
    right: -118px;
    z-index: 1;
    list-style: none;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;

    &.show {
      right: 0;
      transition: 0.5s ease-out 0.2s;
    }

    &.hide {
      right: -118px;
      transition: 0.5s ease-out 0.2s;
    }

    @include media($sm) {
      right: -40vh;
    }

    @include media($md) {
      flex-flow: row;
      align-items: center;
      position: sticky;
    }

    @include media($lg) {
      position: sticky;
    }

    li {
      background-color: $color1;
      width: 100%;

      @include media($md) {
        background: none;
      }
      @include media($lg) {
        background: none;
        width: 10rem;
        margin-left: 1rem;
      }
    }

    li > a {
      width: 100%;
      border-bottom: 1px solid white;
      display: inline-block;
      text-decoration: none;
      color: $font-white;
      opacity: 0.6;
      transition-duration: 0.3s;
      padding: 0.8rem;

      @include media($sm) {
        height: 3.5rem;
        width: 40vh;
        line-height: 3.5rem;
        padding: 0 0.8rem;
      }
      @include media($md) {
        height: auto;
        width: 17vw;
        line-height: 1;
        text-align: center;
        font-size: 1.3rem;
        border-bottom: none;
        padding: 0;
      }

      @include media($xl) {
        font-size: 1.6rem;
      }

      &:hover {
        border-bottom: 2px solid $font-white;
        padding-bottom: 10px;
        opacity: 1;
      }
    }
  }
}

// HAMBURGER MENU
.page-nav-toggle {
  width: 80px;
  height: 80px;
  background: transparent;
  border: none;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  text-align: right;
  margin-right: 0.5rem;

  @include media($md) {
    display: none;
  }

  span {
    display: block;
    width: 40px;
    height: 4px;
    background-color: #fff;
    align-self: stretch;
    margin: 5px 0;
  }
}

// TEXT BANNER
.main-banner {
  grid: 2;
  color: $font-white;
  opacity: 0.6;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  margin: 0.8rem;
  overflow: hidden;

  @include media($sm) {
    margin: 0;
    justify-content: flex-start;
  }

  @include media($md) {
    margin: 0;
    justify-content: center;
  }

  &-title {
    text-transform: uppercase;
    font-weight: 300;

    @include media($sm) {
      font-size: 1.5rem;
    }

    @include media($md) {
      font-size: 3rem;
      padding-bottom: 2rem;
    }

    @include media($lig) {
      font-size: 2.375rem;
      padding-bottom: 0;
    }

    @include media($lg) {
      font-size: 3.4rem;
    }

    @include media($xl) {
      margin-bottom: 3rem;
    }
  }

  &-text {
    text-align: center;
    margin-bottom: 2rem;
    opacity: 0.7;

    @include media($sm) {
      font-size: 0.875rem;
      padding: 0 0.2rem;
    }

    @include media($md) {
      font-size: 1.2rem;
      padding: 0 0.5rem;
      padding-bottom: 2rem;
    }

    @include media($lig) {
      font-size: 1rem;
      padding: 0 0.5rem;
    }

    @include media($lg) {
      font-size: 1.8rem;
    }

    @include media($xl) {
      padding: 0 2rem;
      font-size: 1.5rem;
      margin-bottom: 3rem;
    }
  }

  &-btn {
    border: 1px solid #fff;
    border-radius: 50px;
    color: $font-white;
    font-weight: 600;
    padding: 20px 30px;
    text-decoration: none;
    text-transform: uppercase;
    transition-duration: 0.4s;

    @include media($sm) {
      padding: 15px 20px;
    }

    @include media($md) {
      font-size: 1.5rem;
      padding: 15px 30px;
      margin-bottom: 2rem;
    }

    @include media($lig) {
      font-size: 1.2rem;
      padding: 10px 20px;
      margin: 0;
    }

    @include media($lg) {
      font-size: 1.8rem;
    }

    @include media($xl) {
      padding: 20px 30px;
    }

    &:hover {
      background-color: #fff;
      color: #000;
      opacity: 0.6;
    }
  }

  .scrollbar {
    border: 1px solid #fff;
    border-radius: 50px;
    display: block;
    width: 2rem;
    height: 4rem;
    justify-self: center;
    position: relative;
    bottom: -4rem;
    margin-top: 3rem;

    @include media($xs) {
      bottom: -7rem;
    }

    @include media($sm) {
      width: 1.5rem;
      height: 3rem;
      bottom: 2rem;
    }

    @include media($mid) {
      width: 2rem;
      height: 4rem;
      bottom: 0;
    }

    @include media($md) {
      width: 3rem;
      height: 6rem;
      bottom: -11rem;
    }

    @include media($lig) {
      width: 2rem;
      height: 4rem;
      bottom: 2rem;
    }

    @include media($lg) {
      bottom: -10rem;
    }

    @include media($xl) {
      bottom: -5rem;
      width: 3rem;
      height: 6rem;
    }

    &::after {
      content: "";
      //display: block;
      width: 1.5rem;
      height: 1.5rem;
      background-color: #fff;
      border-radius: 50%;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%, 20%);
      animation: scrollbar1 1s infinite alternate backwards linear;
      -webkit-animation: scrollbar1 1s infinite alternate backwards linear;

      @include media($sm) {
        width: 1rem;
        height: 1rem;
      }

      @include media($mid) {
        width: 1.3rem;
        height: 1.3rem;
      }

      @include media($md) {
        width: 2rem;
        height: 2rem;
      }

      @include media($lig) {
        width: 1.3rem;
        height: 1.3rem;
      }

      @include media($xl) {
        width: 2rem;
        height: 2rem;
      }
    }
  }
}

// dolny banner
.main-hero-bottom-bar {
  position: absolute;
  max-width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: 2fr minmax(260px, 1fr) 2fr;
  grid-template-rows: 50px;
  justify-items: stretch;

  .bar-left {
    grid-column: 1;
    background-color: #fff;
  }

  .bar-main {
    grid-column: 2;
  }

  .bar-right {
    grid-column: 3;
    background-color: #fff;
  }
  @include media($sm) {
    border-bottom: none;
  }
}

// animacja scrolowania
@keyframes scrollbar1 {
  0% {
    transform: translate(-50%, 20%);
  }

  100% {
    transform: translate(-50%, 145%);
  }
}
